h1 {
color: black;
text-align: center;
}

.form-container {
display: flex;
justify-content: center;
margin-top: 30px;
}

input[type="text"] {
padding: 10px;
border-radius: 5px;
margin-right: 5px;
border: none;
width: 70%;
}

button[type="submit"] {
background-image: linear-gradient(60deg, #fbed96 0%, #abecd6 100%);
color: darkblue;
padding: 10px;
border: none;
border-radius: 5px;
cursor: pointer;
}

button[type="submit"]:hover {
background-image: linear-gradient(60deg, #fbed96 0%, #5bba9a 100%);
}

ul {
list-style: none;
padding: 0;
width: 70%;
margin: 30px auto;
}

li {
background-color: white;
padding: 10px;
border-radius: 5px;
margin-bottom: 10px;
display: flex;
justify-content: space-between;
align-items: center;
}

li .actions {
display: flex;
}

li .actions button {
background-color: #f44336;
color: white;
padding: 5px;
border: none;
border-radius: 5px;
margin-left: 10px;
cursor: pointer;
}

li .actions button.edit {
background-image: linear-gradient(60deg, #96deda 0%, #50c9c3 100%);
}

li .actions button.edit:hover {
background-image: linear-gradient(60deg, #96deda 0%, #1e8782 100%);
}

li .actions button.delete {
background-image: linear-gradient(to right, #ec8c69 0%, #ed6ea0 100%);
}

li .actions button.delete:hover {
background-image: linear-gradient(to right, #ec8c69 0%, #b03766 100%);
}

li .actions button.complete {
  background-image: linear-gradient(to right, #ec77ab 0%, #7873f5 100%);
  }

li .actions button.complete:hover {
  background-image: linear-gradient(to right, #ec77ab 0%, #25238c 100%);
  }

  li .actions button.move-up {
    background-image: linear-gradient(120deg, #f5d671 0%, #fda085 100%);
  }

  li .actions button.move-down {
background-image: linear-gradient(to top, #85eeee 0%, #aa83da 100%);
  }